"use client";

import React, { useState } from 'react';
import { useRouter } from 'next/navigation';
import Header from '@/components/Layout/Header';
import FileUploader from '@/components/FileUpload/FileUploader';
import LufsFAQ from '@/components/FAQ/LufsFAQ';
import { analyzeAudio } from '@/services/audioAnalyzer';

export default function HomePage() {
  const router = useRouter();
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleFileUpload = async (file: File) => {
    try {
      setIsAnalyzing(true);
      setError(null);
      
      const result = await analyzeAudio(file);
      
      // 将结果存储到localStorage
      localStorage.setItem('audioAnalysisResult', JSON.stringify(result));
      
      // 跳转到结果页面
      router.push('/results');
    } catch (err) {
      setError(err instanceof Error ? err.message : '分析过程中发生错误');
    } finally {
      setIsAnalyzing(false);
    }
  };

  const handleNavigate = (page: string) => {
    if (page === 'home') {
      router.push('/');
    } else if (page === 'results') {
      router.push('/results');
    }
  };

  return (
    <>
      <Header activePage="home" onNavigate={handleNavigate} />
      <div className="container mx-auto p-8">
        <h1 className="text-3xl font-bold mb-8 text-gray-800 tracking-tight">音频响度分析</h1>
        <FileUploader onFileSelected={handleFileUpload} />
        <LufsFAQ />
      </div>
    </>
  );
}
